@use 'abstracts/mixins' as *;

.vertical-nav {
  position: fixed;
  right: 48px;
  top: calc(50% - 65px);
  transform: translateY(-50%);
  z-index: 3;

  @include md {
    right: 13px;
  }

  &__item {
    height: 26px;
    display: block;
    width: 2px;
    background: var(--green-light);
    margin: 4px 0;

    &.active,
    &:hover {
      background: var(--amber);
    }
  }
}

// JUMBOTRON

.jumbotron {
  min-height: 100vh;
  background-size: cover;
  display: flex;
  flex-direction: column;
  position: relative;

  &__movie {
    position: absolute;
    top: 0;
    min-width: 100%;
    min-height: 100%;
    left: 0;
    z-index: 2;

    @include md {
      min-height: auto;
    }

    &__container {
      position: absolute;
      top: 0;
      min-width: 100%;
      min-height: 100%;
      left: 0;
      z-index: 2;
      overflow: hidden;

      &::after {
        background: rgba(22, 22, 22, 0.25);
        content: '';
        display: block;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 2;
      }
    }
  }

  &__top {
    flex-grow: 1;
    display: flex;
    position: relative;
    z-index: 3;
    flex-direction: column;
  }

  &__content {
    z-index: 2;
    flex-grow: 1;
    display: flex;
    align-items: flex-end;
    height: 100%;
    padding-bottom: 89px !important;

    &__text {
      max-width: 728px;
      padding-left: 3px;
      font-weight: 300;
      font-size: 50px;
      line-height: 60px;
      letter-spacing: 2.5px;
      text-transform: uppercase;

      @media screen and (min-width: 992px) and (max-height: 900px) {
        font-size: 40px;
      }

      @include md {
        padding-right: 10px;
        padding-left: 0;
        font-size: 30px;
        line-height: 36px;
        letter-spacing: 1.5px;
      }
    }

    @include md {
      padding-bottom: 55px !important;
    }
  }

  &__info {
    background-color: rgba(#162a26, 0.9);
    padding-top: 40px;
    padding-bottom: 45px;
    position: relative;
    z-index: 2;
    @media screen and (min-width: 992px) and (max-height: 900px) {
      padding-top: 26px;
      padding-bottom: 28px;
    }

    @include md {
      padding-top: 26px;
      padding-bottom: 28px;
    }

    .container {
      display: flex;
      justify-content: flex-end;
    }

    &__dots {
      background: url('./../assets/home-header-dots.webp');
      flex-grow: 1;
      background-size: 75%;
      background-repeat: no-repeat;
      margin-top: 10px;
      position: relative;
      flex-shrink: 0;
      left: -16px;
      display: flex;
      align-items: flex-end;

      @include md {
        background: url('./../assets/dots-mobile.webp');
        background-repeat: no-repeat;
        margin-top: 5px;
        width: 30px;
        height: 57px;
        margin-right: 15px;
        background-size: contain;
        position: static;
      }

      &__button {
        max-width: 250px;

        @include md {
          display: none;
        }
      }
    }

    &__button {
      max-width: 250px;
      margin: 40px 0 0 0;
      display: none;
      @include md {
        display: flex;
      }
    }

    &__text {
      margin-bottom: 10px;

      @include md {
        margin-bottom: 0;
      }

      &:last-of-type {
        margin-bottom: 0;
      }
    }

    &__text-container {
      width: 60%;
      position: relative;
      max-width: 626px;

      @media screen and (min-width: 992px) and (max-height: 900px) {
        width: 65%;
        max-width: 700px;
      }

      @include md {
        width: 100%;
        margin-left: -4px;
        padding-right: 5px;
      }

      &::before {
        content: '';
        display: block;
        background-color: var(--gold);
        width: 2px;
        height: 425px;
        position: absolute;
        top: 0;
        left: -63px;

        @include md {
          display: none;
        }
      }
    }

    &__links {
      margin-top: 25px;
      display: flex;

      @include md {
        display: block;
        margin-top: 18px;
      }

      .button {
        margin-right: 34px;
        margin-top: -4px;
        margin-bottom: 13px;
        font-size: 13px;
        letter-spacing: 1.3px;

        &:last-of-type {
          margin-right: 0;
        }

        @include md {
          margin-right: 0;
          margin-top: -1px;
          font-size: 11px;
          letter-spacing: 1.1px;
        }
      }
    }
  }
}

.white-papers {
  background: var(--light-grey);
  padding: 60px 0 93px 0;
  overflow: hidden;

  @include md {
    padding: 41px 0;
  }

  &__title {
    margin-bottom: 81px;

    @include md {
      margin-bottom: 26px;
    }
  }

  .slider-container {
    max-width: 820px;
    width: 100%;
    position: relative;

    &::before {
      content: '';
      width: 50vw;
      height: 100%;
      background: var(--light-grey);
      position: absolute;
      top: 0;
      left: -50vw;
      z-index: 3;
    }
  }

  &__slider {
    display: flex;

    @include md {
      padding-left: 0;
    }
  }

  &__top {
    display: flex;
    justify-content: space-between;
  }

  &__controls {
    display: flex;
    outline: none;

    button {
      margin-left: 12px;
      width: 33px;
      height: 33px;
      transition: 0.2s all ease-in;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      border: 1px solid var(--green-light);
      margin-right: 9px;
      flex-shrink: 0;
      background: transparent;
      cursor: pointer;
      outline: none;

      &[disabled] {
        cursor: not-allowed;
      }

      &.prev {
        svg {
          transform: rotate(180deg);
          margin-right: 2px;
        }
      }
      &.next {
        svg {
          margin-left: 2px;
        }
      }

      @include md {
        width: 26px;
        margin-right: 7px;
        height: 26px;
      }

      svg {
        width: 12px;
        height: 17px;

        @include md {
          width: 10px;
          height: 13px;
        }
      }

      &:not([disabled]) {
        &:hover,
        &:focus {
          border-color: var(--gold);
          background: var(--gold);

          svg,
          path {
            stroke: var(--dark-green);
          }
        }
      }
    }
  }

  .tns-ovh {
    overflow: visible;
    @include md {
      overflow: hidden;
    }
  }

  .tns-nav {
    display: none;
  }

  &__item {
    display: flex !important;
    padding-right: 70px;
    width: 820px;

    @include md {
      width: auto;
      display: block !important;
      margin-right: 0;
    }

    &__img {
      width: 374px;
      height: 268px;
      background-size: cover;
      background-repeat: no-repeat;
      background-position: center;

      @include md {
        width: 100%;
        margin-bottom: 23px;
        height: 223px;
      }
    }

    &__text {
      width: 345px;
      padding-left: 28px;
      display: flex;
      flex-direction: column;
      margin-top: -5px;

      @include md {
        width: 100%;
        padding-left: 0;
      }

      &__title {
        margin: 0 0 16px 0;
        color: var(--mid-green);
        letter-spacing: 2px;
        font-size: 20px;
        text-transform: uppercase;
        line-height: 28px;

        @include md {
          font-size: 16px;
          line-height: 24px;
          letter-spacing: 1.6px;
          margin-bottom: 5px;
        }
      }

      &__description {
        color: var(--dark-grey);
        font-family: var(--unnamed-font-family-garamond-premier-pro);
        font-size: 20px;
        font-weight: 300;
        line-height: 25px;
        letter-spacing: 1px;
        flex-grow: 1;

        @include md {
          font-size: 16px;
          line-height: 22px;
          letter-spacing: 0.4px;
          margin-bottom: 45px;
          padding-right: 5px;
        }
      }
    }
  }
}

.how-it-works {
  background: var(--white) url('./../assets/how-it-works-bg.webp') no-repeat;
  background-position: -92px -150px;
  background-size: 579px;
  padding-top: 105px;

  .platform {
    padding-top: 62px;

    @include md {
      padding-top: 43px;
    }
  }

  @include md {
    padding-top: 47px;
    padding-bottom: 43px;
    background-size: 500px;
    background-position: -250px 250px;
  }

  &__content {
    display: flex;

    @include md {
      display: block;
    }
  }

  &__slider-container {
    flex-grow: 1;
  }

  &__text-container {
    width: 60%;
    position: relative;
    max-width: 626px;
    flex-shrink: 0;
    margin-top: -3px;

    @include md {
      width: 100%;
      margin-top: 24px;
    }

    .typography-text {
      line-height: 28px;
      letter-spacing: 0.5px;
      margin-bottom: 27px;

      @include md {
        font-size: 16px;
        line-height: 22px;
        margin-bottom: 22px;
        letter-spacing: 0.4px;
      }

      &:last-of-type {
        margin-bottom: 20px;

        @include md {
          margin-bottom: 23px;
        }
      }
    }
  }

  .tns-outer {
    width: 293px;

    @include md {
      width: 100%;
    }
  }

  .tns-nav {
    display: flex;
    justify-content: center;
    margin-top: 16px;

    @include md {
      margin-top: 8px;
    }

    button {
      width: 12px;
      height: 12px;
      border-radius: 50%;
      border: 1px solid var(--green-light);
      background: transparent;
      margin: 0 6px;
      outline: none;
      padding: 0;

      &.tns-nav-active,
      &:hover {
        background: var(--gold);
        border-color: var(--gold);
      }
    }
  }

  &__slider {
    margin-top: 35px;

    @include md {
      margin-top: 26px;
    }

    &__item {
      margin-bottom: 25px;
      text-align: center;

      &__icon {
        margin-bottom: 33px;
      }

      &__title {
        text-align: center;
        font-size: 20px;
        line-height: 25px;
        letter-spacing: 1px;
        color: var(--dark-green);
        margin-bottom: 10px;
        font-family: var(--unnamed-font-family-garamond-premier-pro);

        @include md {
          font-size: 16px;
          line-height: 22px;
          letter-spacing: 0.4px;
          margin-top: -2px;
          margin-bottom: 16px;
        }
      }

      &__value {
        text-align: center;
        font-size: 50px;
        letter-spacing: 1.75px;
        text-transform: uppercase;
        color: var(--green-light);
        font-weight: 300;
      }
    }
  }
}

.platform {
  background: var(--white);

  img {
    max-width: 1071px;
    height: auto;
    display: block;
    width: 100%;
    margin: 0 auto;
  }
}

.join {
  background: var(--white);

  &__logo {
    display: flex;
    justify-content: center;
    padding: 69px 0 23px 0;

    @include md {
      padding-top: 53px;
    }

    svg,
    path,
    g {
      fill: var(--mid-green);
    }
  }

  &__title {
    font-size: 28px;
    text-align: center;
    line-height: 42px;
    text-transform: uppercase;
    letter-spacing: 2.8px;
    color: var(--green-light);
    margin: 0 0 17px 0;

    @include md {
      font-size: 16px;
      line-height: 24px;
      letter-spacing: 1.6px;
      margin-bottom: 13px;
    }

    &__join {
      color: var(--mid-green);
    }

    &__procure {
      color: var(--dark-green);
    }
  }

  &__text {
    font-family: var(--unnamed-font-family-garamond-premier-pro);
    font-size: 20px;
    line-height: 28px;
    letter-spacing: 0.5px;
    color: var(--dark-grey);
    margin: 0 auto 43px auto;
    text-align: center;
    max-width: 767px;

    @include md {
      font-size: 16px;
      line-height: 22px;
      margin-bottom: 33px;
    }
  }

  &__plans {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding-bottom: 58px;

    @include md {
      padding-bottom: 12px;
    }
  }

  &__plan {
    background: var(--light-grey);
    width: calc(50% - 2px);
    margin-bottom: 13px;
    display: flex;
    min-height: 260px;

    flex-direction: column;
    text-align: center;
    align-items: center;

    @include md {
      width: 100%;
      margin-bottom: 12px;
    }

    &__title {
      font-size: 22px;
      text-align: center;
      text-transform: uppercase;
      letter-spacing: 2.2px;
      color: var(--mid-green);
      margin: 22px 0 13px 0;

      @include md {
        margin: 23px 0 8px 0;
      }
    }

    &__description {
      font-family: var(--unnamed-font-family-garamond-premier-pro);
      font-size: 16px;
      line-height: 22px;
      letter-spacing: 0.4px;
      color: var(--dark-grey);
      flex-grow: 1;
      width: 80%;
      margin-bottom: 30px;
      max-width: 435px;
    }

    &__button {
      max-width: 249px;
      margin-bottom: 35px;

      @include md {
        max-width: 210px;
        margin-bottom: 21px;
      }
    }
  }
}

.esg {
  background: url('./../assets/esg-bg.webp');
  min-height: 776px;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-bottom: 72px;

  @include md {
    min-height: 533px;
    padding-top: 28px;
    padding-bottom: 43px;
    justify-content: flex-start;
    padding-top: 57px;
    background-attachment: scroll !important;
    background-position: center !important;
  }

  &__logo {
    margin: 0 auto 57px auto;
    display: block;

    @include md {
      margin-bottom: 42px;
    }
  }

  &__title {
    text-align: center;
    margin-bottom: 47px;

    font-size: 28px;
    line-height: 38px;
    letter-spacing: 2.8px;
    color: var(--light-grey);
    text-transform: uppercase;

    @include md {
      font-size: 24px;
      line-height: 30px;
      margin-bottom: 39px;
      letter-spacing: 2.4px;
    }
  }

  &__text {
    text-align: center;
    font-family: var(--unnamed-font-family-garamond-premier-pro);
    font-weight: 300;
    font-size: 24px;
    line-height: 30px;
    letter-spacing: 1.2px;
    color: var(--light-grey);
    max-width: 567px;
    margin: 0 auto 31px auto;

    @include md {
      font-size: 16px;
      line-height: 22px;
      letter-spacing: 0.8px;
      margin-bottom: 39px;
      padding: 0 42px;
      margin-bottom: 61px;
    }
  }

  &__button {
    padding-right: 0px;

    &.button-outlined {
      max-width: 415px;

      @include md {
        font-size: 10px;
        height: 39px;
        max-width: 297px;
        padding-left: 21px;
        letter-spacing: 1px;
        position: relative;
        top: -10px;
      }
    }

    &-container {
      display: flex;
      justify-content: center;
      position: relative;
      top: 14px;

      @include md {
        position: static;
      }
    }
  }
}
